import { ref } from 'vue'

const getmainArticle = () => {
    const mainarticle = ref([])
    const error = ref(null)

    const load = async () => {
      try{
        let data = await fetch('https://my-json-server.typicode.com/farhanade40/api/articles/2')
        if(!data.ok){
          throw Error('no data available')
        }
        mainarticle.value = await data.json()
      }catch (err) {
        error.value = err.message
        console.log(error.value)
      }
    }
        return { mainarticle, error, load}
}
 

export default getmainArticle