<script>
import getArticle from '../composables/getArticle'
import axios from 'axios'
import Footer from '../components/Footer'




export default{ 
    components : { Footer },
    head: {
      script: [
        { type: 'text/javascript', src: 'https://www.instagram.com/embed.js', async: true, body: true}, // Insert in body
        // with shorthand
        // ...
      ],
    },
    
    props: ['id'],
    setup(props) {
        const { article, error, load } = getArticle(props.id)

        load()

        return { article, error }
    },
    data(){
    return{
        
      form: {
          id: '',
          category: '',
          title: '',
          banner: '',
          author: '',
          short_description: '',
          
        },
        articles: '',
        
    }
    
  },
  mounted(){
    this.load()
  },
  methods: {
    load(){
        axios.get('https://my-json-server.typicode.com/farhanade40/api/articles').then(res => {
        this.articles = res.data
     
        //respon dari rest api dimasukan ke users
      }).catch ((err) => {
        console.log(err);
      })
    }

}
    }
</script>

