import { ref } from 'vue'

const getArticle = (id) => {
    const article = ref(null)
    const error = ref(null)

    const load = async () => {
      try{
        let data = await fetch('https://my-json-server.typicode.com/farhanade40/api/articles/' + id) 
        if(!data.ok){
          throw Error('no data available')
        }
        article.value = await data.json()
      }
      catch (err) {
        error.value = err.message
        console.log(error.value)
      }
    }
        return { article, error, load}
}
 
export default getArticle